@import 'assets/settings';

/*header*/

@each $socialLink in $socialLinks {
    .socials-block__list-item.#{$socialLink} {
        .socials-block__list-link {
            background-color: color('#{$socialLink}');
        }
    }
}

a {
    text-decoration: none;
    display: inline-block;
}

.site-header {
	 background-color: #fff;
    .container-wrapper {
        background-image: url(../img/main/header-bgi.jpg);
        background-repeat: no-repeat;
        background-position-x: center;
    }
    .container-inner {
    		padding-top: 14px;
        @extend %clearfix;
    }
}

.socials-block {
    &__list {
        &-item {
            position: relative;
            display: inline-block;
            width: 24px;
            height: 24px;
            /* margin-right: 4px;
            &:last-child {
            	margin-right: 0;
            } */
        }
        &-link {
            display: inline-block;
            width: 100%;
            height: 100%;
            -webkit-border-radius: 50%;
            -moz-border-radius: 50%;
            border-radius: 50%;
            .fa {
                position: absolute;
                color: #fff;
                font-size: 12px;
                top: 50%;
                left: 50%;
                -webkit-transform: translate(-50%, -50%);
                -moz-transform: translate(-50%, -50%);
                -ms-transform: translate(-50%, -50%);
                -o-transform: translate(-50%, -50%);
                transform: translate(-50%, -50%);
            }
        }
    }
    &--header {
    	ul {
    		margin-bottom: 14px;
    	}
    }
}

.header-contacts {
    width: 19%;
    float: left;
    text-align: right;
    margin-right: 56px;
    &__phones {
        display: inline-block;
        background-image: url(../img/main/phone.png);
        background-repeat: no-repeat;
        background-position: 0px 8px;
        padding: 5px 5px 5px 20px;
        &-link {
            display: block;
            font-family: Verdana;
            font-size: 13px;
            line-height: 16px;
            color: #333333;
        }
    }
}

.header-main {
	position: relative;
    float: left;
    /* margin-right: 60px; */
    text-align: center;
    width: 50%;
    .header-nav {
        &__list {
            &-item {
            	display: inline-block;
               border-right: 1px solid color('primary');
               &:last-child {
               	border-right: none;
               }
            }
            &-link {
            	display: block;
                padding: 0px 11px;
                font-family: 'Verdana';
                font-size: 13px;
                color: color('primary');
                text-transform: uppercase;
            }
        }
    }
    .header-logo {
    	position: absolute;
    	top: 40px;
    	left: 0;
    	z-index: 9999;
    	&__img {
    		display: block;
    	}
    }
}

.header-action {
	float: right;
	margin-right: 8px;
	text-align: right;
	&__user-block {
		margin-bottom: 37px;
	}
	&__search {
		display: inline-block;
		margin-right: 22px;
	}
	&__login {
		display: inline-block;
		margin-right: 20px;
	}
	&__cart {
		&-text {
			font-family: Verdana;
			color: color('primary');
			font-size: 13px;
			text-transform: uppercase;
			a {
				/* display: inline-block; */
				padding: 5px 12px;
				margin-right: 56px;
			}
		}
	}
}






/*END:header*/
